<template>
    <div class="login_wrap">
        <div class="login" >
          <img src="../img/logo.png" alt="" class="logo">
          <div class="tab_login">
            <span :class="{chiose:notphone}" @click="notphone=true">账号密码登录</span>
            <i class="fenge">|</i>
            <span :class="{chiose: !notphone}"  @click="notphone=false">验证码登录</span>
          </div>
        </div>
      <div class="zhang_login " v-if="notphone">
         <li><span class="iconfont icon-shouji"></span><input v-model="mobile"  @blur="mobilePro()"  maxlength="16" v-focus type="number" placeholder="请输入手机号"><img v-if="username" @click="username=''" src="../img/del.png" alt=""></li>
        <li><span class="iconfont icon-mima"></span><input v-model="password" @blur="passwordPro()" maxlength="16" v-focus type="password" placeholder="请输入密码"><img v-if="password" @click="password=''" src="../img/del.png" alt=""></li>
      </div>
      <div class="show_login" v-if="!notphone">
        <li><span class="iconfont icon-shouji"></span><input v-model="mobile" v-focus @blur="mobilePro()" type="number" placeholder="请输入手机号"><img v-if="mobile" @click="mobile=''" src="../img/del.png" alt=""></li>
        <li class="yanzheng"><span class="iconfont icon-yanzhengma"></span><input v-model="code" @blur="codePro()"  v-focus type="number" placeholder="请输入验证码"><img v-if="code" @click="code=''" src="../img/del.png" alt="">
          <van-button type="default" @click="getCode()" :class="{'sendcode':!isSend,'btn_code':true}" plain>{{codeinfo}}</van-button>
        </li>
      </div>
      <van-button  class="btn-login" type="default" plain @click="tologin">登录</van-button>
      <div class="other_link">
        <router-link  to="/unSafe">忘记密码</router-link>
        <router-link  to="/register">注册账号</router-link>
      </div>

    </div>
</template>

<script>
  import { regMobile } from "@/common/js/Reg";
  import { apiPost } from "@/common/js/api";
  import {  apiGet } from "@/common/js/api";
  import { Toast } from 'vant';
    export default {
        name: "logout",
        data(){
          return{
            notphone:true,
            username: '',
            password: '',
            mobile: '',
            mobilereg:'',
            passwordreg:'',
            userId:'',
            code: '',
            timer:'',
            codeinfo:'获取验证码',
            codereg:'',
            isSend: true,
            codeback:''
          }
        },
      created(){
        if(this.$cookie.get('accessToken')){
          history.go(-1)
        }
      },
      methods:{
        getCode() {
          if(regMobile.test(this.mobile)&&this.isSend){
            apiGet('/account/sms',{cellphone: this.mobile,type:2}).then((res)=>{
              console.log(res)
              if(res.status==200){
                this.codeback=res.data.data.phonecode;
                console.log(this.codeback)
                Toast(res.data.message);
                if (!this.isSend) {
                  return
                }
                this.isSend = false
                let num = 60
                this.codeinfo=`(${num}秒后重试)`
                this.timer = setInterval(() => {
                  num --;
                  this.codeinfo=`(${num}秒后重试)`
                  if (num == 0) {
                    clearInterval(this.timer)
                    this.codeinfo=`获取验证码`
                    this.isSend = true
                  }
                }, 1000)
              }else {
                Toast('验证码发送失败,请重试');
                clearInterval(this.timer)
                this.codeinfo=`获取验证码`
                this.isSend = true
              }
            })
          }else if(this.codeinfo != '获取验证码'){
            Toast('验证码发送频繁,请稍后再试');
          }
          else{
            Toast('请输入正确格式的手机号!');
          }
          /*axios.get('/api/menu',{params:{restaurant_id: 155147,terminal:'web'}}).then((res)=>{
            console.log(res)
          })*/
        },
        mobilePro(){
          let myreg=/^[1][3,4,5,7,8][0-9]{9}$/;
          if (!myreg.test(this.mobile)) {
            Toast('请输入正确的手机号');
          }else {
            this.mobilereg=true;
          }
        },
        passwordPro(){
          let psdreg=/^[\w]{6,12}$/;
          if( !psdreg.test(this.password)){
            Toast('请输入正确格式的密码');
            this.passwordreg=false;
          } else{
            this.passwordreg=true;
          }
        },
        codePro(){
            if( this.codeback==this.code ){
              this.codereg=true;
            }
        },
        tologin(){
          if(this.mobilereg && this.passwordreg){
            apiPost('/account/login',{ cellphone:this.mobile,password: this.password }).then((res)=>{
              console.log(res)
              if( res.data.status ==200){
                Toast(res.data.message);
                this.$cookie.set('id', res.data.data.id, { expires: '1Y' });
                this.$cookie.set('cellphone',res.data.data.cellphone, { expires: '1Y' })
                this.$cookie.set('accessToken', res.data.data.accessToken, { expires: '1Y' });
                this.$router.push('/user/login')
              }else if(res.data.status == 400){
                Toast(res.data.message);
                clearInterval(this.timer)
                this.codeinfo=`获取验证码`
                this.isSend = true
              }
            })
          }else if(this.mobilereg && this.codereg){
            apiPost('/account/login-code',{ cellphone:this.mobile,code:this.code}).then((res)=>{
              console.log(res)
              if( res.data.status ==200){
                Toast(res.data.message);
                this.$cookie.set('id', res.data.data.id, { expires: '1Y' });
                this.$cookie.set('cellphone',res.data.data.cellphone, { expires: '1Y' })
                this.$cookie.set('accessToken', res.data.data.accessToken, { expires: '1Y' });
                this.$router.push('/user/login')
              }else if(res.data.status == 400){
                Toast(res.data.message);
                clearInterval(this.timer)
                this.codeinfo=`获取验证码`
                this.isSend = true
              }
            })
          }
        }
      },

    }
</script>

<style scoped lang="scss">
  .iconfont{
    font-size: 0.19rem;
    color: #BAC0CA;
  }
  p{
    color: #BAC0CA;
  }
  .router-link-active .iconfont,.router-link-active p{
    color:#4E8DF4;
  }
  .login{
    height: 2.17rem;
    background:linear-gradient(-90deg,rgba(70,133,238,1),rgba(127,173,249,1));
    overflow: hidden;
    position: relative;
    .logo{
      width: 2.25rem;
      height: 0.46rem;
      display: block;
      margin: 0.78rem auto 0;
    }
    .tab_login{
      position: absolute;
      display: flex;
      width: 100%;
      height: 0.4rem;
      color: rgba(255,255,255,.5);
      text-align: center;
      font-weight: 400;
      font-size:0.16rem;
      bottom: 0;
      i{font-style: normal}
      span{
        flex: 1;
      }
      .chiose{color: rgba(255,255,255,1);}
    }

  }
  .zhang_login,.show_login{
    margin-top: 0.65rem;
    li{
      list-style: none;
      position: relative;
      width: 3.06rem;
      height: 0.32rem;
        color: #9a9a9a;
      border-bottom: 1px solid #f5f5f5;
      margin: 0 auto .35rem;
      span{
        float: left;
        font-size: 0.2rem;
        margin: 0 0.08rem;
      }
      input{
        font-size: .17rem;
        float: left;
        border: none;
        height: .3rem;
        width: 2.6rem;
      }
      img{
        width: 0.22rem;
        border-radius: 0.22rem;
        right: 0.03rem;
        height: 0.22rem;
        position: absolute;
      }
    }
    .yanzheng{
      position: relative;
      .btn_code{
        width:0.94rem;
        position: absolute;
        right: 0;
        background:linear-gradient(-90deg,rgba(60,127,238,1),rgba(103,154,237,1));
        border-radius:0.04rem;
        color: white;
        font-size: .14rem;
        padding:0;
        line-height: 0.31rem;
        height: 0.31rem;
        bottom: 0.1rem;
        border: none;
      }
      .sendcode{
        background:linear-gradient(-90deg,rgba(178,178,178,1),rgba(201,201,201,1));
        border-radius:0.04rem;
        color: white;
      }
      img{
        right: 1rem;
      }
    }
    .focus{
      border-bottom: 1px solid #3C7FEE;
      color: #333;
      span{
        color: #3C7FEE;
      }
    }
  }
  .btn-login{
    width: 3.06rem;
    height: .47rem;
    background:linear-gradient(-90deg,rgba(60,127,238,1),rgba(103,154,237,1));
    border-radius:0.04rem;
    font-size: 0.17rem;
    text-align: center;
    line-height: .47rem;
    outline: none;
    border: none;
    display: block;
    margin: 0 auto;
    color: white;
  }
  .other_link{
    width: 3.06rem;
    overflow: hidden;
    margin: .36rem auto ;
    a{font-size: .16rem; color: #9a9a9a;}
    a:nth-of-type(1){float: left;}
    a:nth-of-type(2){float: right;}
  }

</style>



